'use client';

import { useState, useEffect } from 'react';
import { Clock, Plus, Edit2, Trash2, Check, X } from 'lucide-react';

type MassTime = {
  id: number;
  day_type: string;
  time: string;
  location: string;
  max_reservations: number;
  is_active: boolean;
  display_order: number;
};

type DayType = 'weekdays' | 'saturdays' | 'sundays';

const DAY_TYPE_LABELS: Record<DayType, string> = {
  weekdays: 'Lunes a Viernes',
  saturdays: 'Sábados',
  sundays: 'Domingos',
};

export default function MassTimesPage() {
  const [massTimes, setMassTimes] = useState<MassTime[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingTime, setEditingTime] = useState<MassTime | null>(null);
  const [formData, setFormData] = useState({
    day_type: 'weekdays' as DayType,
    time: '',
    location: 'Parroquia',
    max_reservations: 1,
    is_active: true,
    display_order: 0,
  });

  useEffect(() => {
    fetchMassTimes();
  }, []);

  const fetchMassTimes = async () => {
    try {
      const response = await fetch('/api/admin/mass-times');
      const data = await response.json();
      if (data.success) {
        setMassTimes(data.data);
      }
    } catch (error) {
      console.error('Error al cargar horarios:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    try {
      const url = editingTime 
        ? `/api/admin/mass-times/${editingTime.id}`
        : '/api/admin/mass-times';
      
      const method = editingTime ? 'PUT' : 'POST';
      
      const response = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData),
      });

      const data = await response.json();

      if (data.success) {
        fetchMassTimes();
        closeModal();
        alert(editingTime ? 'Horario actualizado' : 'Horario creado');
      } else {
        alert(data.error || 'Error al guardar');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Error al guardar el horario');
    }
  };

  const handleDelete = async (id: number) => {
    if (!confirm('¿Estás seguro de eliminar este horario?')) return;

    try {
      const response = await fetch(`/api/admin/mass-times/${id}`, {
        method: 'DELETE',
      });

      const data = await response.json();

      if (data.success) {
        fetchMassTimes();
        alert('Horario eliminado');
      } else {
        alert(data.error || 'Error al eliminar');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Error al eliminar el horario');
    }
  };

  const toggleActive = async (massTime: MassTime) => {
    try {
      const response = await fetch(`/api/admin/mass-times/${massTime.id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ is_active: !massTime.is_active }),
      });

      const data = await response.json();

      if (data.success) {
        fetchMassTimes();
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const openModal = (massTime?: MassTime) => {
    if (massTime) {
      setEditingTime(massTime);
      setFormData({
        day_type: massTime.day_type as DayType,
        time: massTime.time,
        location: massTime.location,
        max_reservations: massTime.max_reservations,
        is_active: massTime.is_active,
        display_order: massTime.display_order,
      });
    } else {
      setEditingTime(null);
      setFormData({
        day_type: 'weekdays',
        time: '',
        location: 'Parroquia',
        max_reservations: 1,
        is_active: true,
        display_order: 0,
      });
    }
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setEditingTime(null);
  };

  const groupedTimes = massTimes.reduce((acc, time) => {
    if (!acc[time.day_type]) {
      acc[time.day_type] = [];
    }
    acc[time.day_type].push(time);
    return acc;
  }, {} as Record<string, MassTime[]>);

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Cargando horarios...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">Horarios de Misas</h1>
              <p className="text-gray-600 mt-1">Gestiona los horarios disponibles para reservas</p>
            </div>
            <button
              onClick={() => openModal()}
              className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              <Plus className="w-5 h-5" />
              Nuevo Horario
            </button>
          </div>
        </div>

        {/* Horarios agrupados por día */}
        <div className="grid gap-6">
          {(['weekdays', 'saturdays', 'sundays'] as DayType[]).map((dayType) => (
            <div key={dayType} className="bg-white rounded-lg shadow-sm p-6">
              <h2 className="text-xl font-semibold text-gray-900 mb-4">
                {DAY_TYPE_LABELS[dayType]}
              </h2>
              
              {groupedTimes[dayType]?.length > 0 ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                  {groupedTimes[dayType].map((massTime) => (
                    <div
                      key={massTime.id}
                      className={`border rounded-lg p-4 ${
                        massTime.is_active ? 'border-gray-200' : 'border-red-200 bg-red-50'
                      }`}
                    >
                      <div className="flex items-start justify-between">
                        <div className="flex items-center gap-2">
                          <Clock className="w-5 h-5 text-blue-600" />
                          <div>
                            <p className="font-semibold text-gray-900">{massTime.time}</p>
                            <p className="text-sm text-gray-600">{massTime.location}</p>
                            <p className="text-xs text-gray-500">
                              Max: {massTime.max_reservations} reserva(s)
                            </p>
                          </div>
                        </div>
                        <div className="flex gap-2">
                          <button
                            onClick={() => toggleActive(massTime)}
                            className={`p-1 rounded ${
                              massTime.is_active
                                ? 'text-green-600 hover:bg-green-50'
                                : 'text-red-600 hover:bg-red-50'
                            }`}
                            title={massTime.is_active ? 'Desactivar' : 'Activar'}
                          >
                            {massTime.is_active ? <Check className="w-4 h-4" /> : <X className="w-4 h-4" />}
                          </button>
                          <button
                            onClick={() => openModal(massTime)}
                            className="p-1 text-blue-600 hover:bg-blue-50 rounded"
                            title="Editar"
                          >
                            <Edit2 className="w-4 h-4" />
                          </button>
                          <button
                            onClick={() => handleDelete(massTime.id)}
                            className="p-1 text-red-600 hover:bg-red-50 rounded"
                            title="Eliminar"
                          >
                            <Trash2 className="w-4 h-4" />
                          </button>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-gray-500 italic">No hay horarios configurados para este día</p>
              )}
            </div>
          ))}
        </div>
      </div>

      {/* Modal */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-md w-full p-6">
            <h2 className="text-2xl font-bold mb-4">
              {editingTime ? 'Editar Horario' : 'Nuevo Horario'}
            </h2>
            
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Día *
                </label>
                <select
                  value={formData.day_type}
                  onChange={(e) => setFormData({ ...formData, day_type: e.target.value as DayType })}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  required
                >
                  <option value="weekdays">Lunes a Viernes</option>
                  <option value="saturdays">Sábados</option>
                  <option value="sundays">Domingos</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Hora *
                </label>
                <input
                  type="text"
                  value={formData.time}
                  onChange={(e) => setFormData({ ...formData, time: e.target.value })}
                  placeholder="ej: 6:00 a.m."
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Ubicación
                </label>
                <input
                  type="text"
                  value={formData.location}
                  onChange={(e) => setFormData({ ...formData, location: e.target.value })}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Máximo de reservas
                </label>
                <input
                  type="number"
                  min="1"
                  value={formData.max_reservations}
                  onChange={(e) => setFormData({ ...formData, max_reservations: parseInt(e.target.value) })}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Orden de visualización
                </label>
                <input
                  type="number"
                  min="0"
                  value={formData.display_order}
                  onChange={(e) => setFormData({ ...formData, display_order: parseInt(e.target.value) })}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                />
              </div>

              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="is_active"
                  checked={formData.is_active}
                  onChange={(e) => setFormData({ ...formData, is_active: e.target.checked })}
                  className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
                />
                <label htmlFor="is_active" className="ml-2 text-sm text-gray-700">
                  Horario activo
                </label>
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  type="button"
                  onClick={closeModal}
                  className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50"
                >
                  Cancelar
                </button>
                <button
                  type="submit"
                  className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
                >
                  {editingTime ? 'Actualizar' : 'Crear'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
